.main-content {
  @media (max-width: 600px) {
    padding-top: 40px !important;
  }
}
.header-main-div {
  .navbar {
    height: 80px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1.2rem;
  }
  .navbar-logo {
    color: #fff;
    justify-self: start;
    margin-left: 20px;
    cursor: pointer;
  }

  .nav-menu {
    display: grid;
    grid-template-columns: repeat(5, auto);
    // gap: 20px;
    list-style: none;
    text-align: center;
    width: 70vw;
    justify-content: end;
    margin-right: 2rem;
  }
  .nav-links {
    color: #fff;
    text-decoration: none;
    padding: 0.5rem 1rem;
  }
  .nav-links:hover {
    background-color: #6d76f7;
    border-radius: 4px;
    transition: all 0.2s ease-out;
  }
  .fa-bars {
    color: #fff;
  }
  .nav-links-mobile {
    display: none;
  }
  .menu-icon {
    display: none;
  }
  @media (max-width: 900px) {
    .navbar {
      position: relative;
    }
    .nav-menu {
      display: flex;
      flex-direction: column;
      width: 100%;
      // height: 100vh;
      position: absolute;
      top: 80px;
      left: -100%;
      opacity: 1;
      transition: all 0.5s ease;
    }
    .nav-menu.active {
      // background: #6668f4;
      left: 0;
      opacity: 1;
      background: black;
      padding-bottom: 10px;
      transition: all 0.5s ease;
      z-index: 1;
    }
    .nav-links {
      text-align: center;
      padding: 2rem;
      width: 100%;
      display: table;
    }
    .nav-links:hover {
      background-color: #7577fa;
      border-radius: 0;
    }
    .navbar-logo {
      position: absolute;
      margin-left: 0;
      top: 0;
      left: 0;
      transform: translate(25%, 50%);
    }
    .menu-icon {
      display: block;
      position: absolute;
      top: 0;
      right: 0;
      transform: translate(-100%, 60%);
      font-size: 1.8rem;
      cursor: pointer;
    }
    .fa-times {
      color: #fff;
      font-size: 2rem;
    }
    .nav-links-mobile {
      display: block;
      text-align: center;
      padding: 1.5rem;
      margin: 2rem auto;
      border-radius: 4px;
      width: 80%;
      background: #4ad9e4;
      text-decoration: none;
      color: #fff;
      font-size: 1.5rem;
    }

    .nav-links-mobile:hover {
      background: #fff;
      color: #6568f4;
      transition: 250ms;
    }

    Button {
      display: none;
    }
  }

  p {
    margin: 0;
    font-family: "Inter-Medium";
    cursor: pointer;
  }
  .login {
    background: linear-gradient(
        90deg,
        #b827dc 0%,
        #a322de 26%,
        #4190f5 86%,
        #6bdef8 100%
      )
      0% 0% no-repeat padding-box;
    border-radius: 50em;
    font-family: "Inter-Medium";
    font-size: 15px;
    border: 4px solid transparent;
    color: white;
    padding: 5px 2.25rem;
    cursor: pointer;
  }
}
