.subscpition-main-div {
  top: 20% !important;
  font-size: 60px;
  font-family: "Inter-Medium";
  // display: flex;
  .scrollable-content {
    padding-left: 50%;
  }
  .text-div {
    line-height: 1;
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 50%;
    .subs-div {
      font-size: 50px;
      font-weight: bold;
      background: transparent
        linear-gradient(
          90deg,
          #b827dc 0%,
          #a322de 26%,
          #4190f5 86%,
          #6bdef8 100%
        );
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;
      line-height: 1.5;
    }
    .startearning-div button {
      background: linear-gradient(black, black) padding-box,
        linear-gradient(
          90deg,
          #b827dc 0%,
          #a322de 26%,
          #4190f5 86%,
          #6bdef8 100%
        );
      border-radius: 50em;
      border: 4px solid transparent;
      color: white;
      padding: 1.5rem 5rem;
      cursor: pointer;
      font-size: 18px;
    }
  }
  .phone-rightside {
    padding-left: 57px;
    max-width: 155px;
    h1 {
      font-size: 150px;
      font-family: "Inter-Bold";
    }
    h6 {
      font-size: 20px;
      font-family: "Inter-Regular";
      margin-top: 1rem;
    }
  }
  input {
    height: 30px;
    width: 180px;
    background: black;
    border: 1px solid #585858;
    border-radius: 50rem;
  }
  .join-btn {
    background: linear-gradient(
      90deg,
      #b827dc 0%,
      #a322de 26%,
      #4190f5 86%,
      #6bdef8 100%
    );
    background-repeat: no-repeat;
    border-radius: 50em;
    font-family: "Inter-Medium";
    font-size: 13px;
    // border: 4px solid transparent;
    color: white;
    padding: 0.5rem 4rem;
    outline: none !important;
    cursor: pointer;
    margin-top: 1.7rem;
  }
  .scrollable-content {
    // overflow-y: scroll;
    overflow-x: hidden !important;
  }



  .section-container {
    display: flex;
    align-items: stretch;
  }

  .left-side {
    flex: 1;
    padding: 20px;
    background-color: lightgray;
  }

  .right-side {
    flex: 1;
    display: flex;
    flex-direction: column;
    overflow: hidden;
  }

  .scrollable-box {
    flex: 1;  
    padding: 20px;
    background-color: white;
  }

  .below-content {
    padding: 20px;
    background-color: lightgray;
  }
}

.back-div {
  position: absolute;
  // width: 100%;
  display: flex;
  justify-content: center;
  align-items: end;
  z-index: 999;
  margin-top: 358px;
  height: 235px;
  width: 248px;
  margin-left: 10px;

  .img-content {
    // margin-top: 200px;

    background: linear-gradient(
      to bottom,
      rgba(145, 144, 144, 0),
      rgba(0, 0, 0, 0.7)
    );
    width: 260px;
    padding-left: 15px;
    padding-right: 15px;

    // border-radius: 0px 0px 20px 20px;
  }
  .form-check {
    .tabs {
      background-color: #4a4a4a;
      box-shadow: 0 0 1px 0 rgba(24, 94, 224, 0.15),
        0 6px 12px 0 rgba(24, 94, 224, 0.15);
      border-radius: 99px;
      width: fit-content !important;
    }

    .tabs * {
      z-index: 2;
    }

    .tab {
      height: 28px;
      width: 115px;
      font-size: 11px;
      color: rgb(178, 178, 178);
      font-weight: 500;
      border-radius: 99px;
      cursor: pointer;
      transition: color 0.15s ease-in;
    }

    .glider {
      position: absolute;
      display: flex;
      height: 28px;
      width: 115px;
      background: linear-gradient(
          90deg,
          #b827dc 0%,
          #a322de 26%,
          #4190f5 86%,
          #6bdef8 100%
        )
        0% 0% no-repeat padding-box;
      z-index: 1;
      border-radius: 99px;
      transition: 0.25s ease-out;
    }
    .main-tab {
      input[type="radio"] {
        display: none;
      }
      input[type="radio"]:checked + label {
        color: white;
      }

      input[type="radio"]:checked + label > .notification {
        background: linear-gradient(
            90deg,
            #b827dc 0%,
            #a322de 26%,
            #4190f5 86%,
            #6bdef8 100%
          )
          0% 0% no-repeat padding-box;
        border-radius: 50em;
        font-family: "Inter-Medium";
        font-size: 15px;
        border: 4px solid transparent;
        color: white;
        padding: 5px 2.25rem;
        cursor: pointer;
        color: #fff;
        margin: 0px;
      }

      input[id="radio-1"]:checked ~ .glider {
        transform: translateX(0);
      }

      input[id="radio-2"]:checked ~ .glider {
        transform: translateX(100%);
      }

      input[id="radio-3"]:checked ~ .glider {
        transform: translateX(0);
      }

      input[id="radio-4"]:checked ~ .glider {
        transform: translateX(100%);
      }

      input[id="radio-5"]:checked ~ .glider {
        transform: translateX(0);
      }

      input[id="radio-6"]:checked ~ .glider {
        transform: translateX(100%);
      }
    }
  }
}

.subscription {
  background: linear-gradient(
      90deg,
      #b827dc 0%,
      #a322de 26%,
      #4190f5 86%,
      #6bdef8 100%
    )
    0% 0% no-repeat padding-box;
  border-radius: 50em;
  font-family: "Inter-Medium";
  font-size: 12px;
  border: 4px solid transparent;
  color: white;
  padding: 5px 2.25rem;
  width: 100%;
  cursor: pointer;
}

.progress-bar {
  background: linear-gradient(
      90deg,
      #b827dc 0%,
      #a322de 26%,
      #4190f5 86%,
      #6bdef8 100%
    )
    0% 0% no-repeat padding-box !important;
}

